import React, { Component } from 'react';
import {Button} from 'react-bootstrap';
import axios from 'axios'

class Login extends Component {

   login(){
    axios.get(window.BASE_URL + '/users/auth/amazon')
    .then(res => {
      return res
    })
  }
  render() {
    return (
      <div>
        <Button bsStyle="primary" id="LoginWithAmazon" onClick={() => this.login()}><img border="0" alt="Login with Amazon"
          src="http://g-ecx.images-amazon.com/images/G/01/lwa/btnLWA_gold_312x64.png"
          width="156" height="32" /></Button>
      </div>
    );
  }
}
export default Login;



